* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}

body {
  color: #fff;
  background-color: #f4f5f7;
  font-family: Lato, sans-serif;
}

.momentum {
  height: 100vh;
  background-image: url("https://images.pexels.com/photos/998641/pexels-photo-998641.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-position: center;
  background-size: cover;
  padding: 2rem 3rem;
}

.momentum-link a {
  color: #fff;
  font-size: 1.6rem;
  text-decoration: none;
  display: inline-block;
}

.momentum-nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.weather {
  width: 10%;
  align-items: flex-start;
  display: flex;
}

.weather-icon {
  width: 24%;
  margin-right: 1.2rem;
}

.momentum-hero {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.momentum-time {
  margin-bottom: .8rem;
  font-size: 9.6rem;
  font-weight: 400;
}

.momentum-greeting {
  margin-bottom: .4rem;
  font-size: 4.2rem;
}

.momentum-text {
  margin: 1.6rem 0;
  font-size: 3.8rem;
}

.momentum-focus {
  font-size: 3.6rem;
  display: inline-block;
}

.momentum-quote {
  text-align: center;
  position: absolute;
  bottom: 1%;
  left: 50%;
  transform: translateX(-50%);
}

.quote-text {
  font-size: 1.6rem;
}

.quote-author {
  margin-top: .3rem;
  font-size: 1.6rem;
  font-weight: 700;
}

